<template>
  <div style=" text-align:center;">
    <el-row>
      <el-col :span="24">
        <carousel></carousel>
      </el-col>
    </el-row>
    <div style="margin-bottom: 2rem">
      <div style="padding-top: 2rem;padding-bottom: 2rem">
        <span style="font-size: 1.5rem;color:black"></span>
        <div style="margin-top: 0.8rem">
          <span style="font-size: 1.2rem;color:black;">Contact us</span>
        </div>
      </div>
      <el-row style="margin-top:2rem" >
        <el-col :span="4" :offset="4">
          <span style="font-size: 1.2rem;color:black"></span>
          <div style="margin-top: 0.5rem">
            <span >400xxx8888</span>
          </div>
        </el-col>
        <el-col :span="4" >
          <span style="font-size: 1.2rem;color:black"></span>
          <div style="margin-top: 0.5rem">
            <span >name@example.xxx</span>
          </div>
        </el-col>
        <el-col :span="4" >
          <span style="font-size: 1.2rem;color:black"></span>
          <div style="margin-top: 0.5rem">
            <span >010xxxx8888</span>
          </div>
        </el-col>
        <el-col :span="4" >
          <span style="font-size: 1.2rem;color:black"></span>
          <div style="margin-top: 0.5rem">
           <span >010xxxx8888</span>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-row style="margin-bottom: 2rem">
      <el-col :span="8" :offset="4">
        <div id="allmap" style="height: 24rem;width: 100%"></div>
      </el-col>
      <el-col :span="8" >
        <el-form ref="form"  :model="form" label-width="100px" >
          <el-form-item>
            <el-input style="line-height: 1rem" placeholder="" v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input  type="textarea" :rows="6" placeholder="" v-model="form.content" ></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="form.email" placeholder=""></el-input>
          </el-form-item>
          <el-form-item >
            <el-col :span="16">
            <el-input v-model="form.code"  placeholder="" ></el-input>
            </el-col>
            <el-col :span="7" :offset="1">
              <img width="100%" :src="imageUrl" @click="getCaptcha"/>
            </el-col>
          </el-form-item>
          <el-form-item style="text-align: right;">
            <el-button type="primary" @click="onSubmit" ></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import carousel from './components/carousel.vue'

  export default {
    name:"aboutUs",
    data() {
      return{
        //
        markerArr : [{title:"",content:"",point:"117.183603|31.847622",isOpen:0,icon:{w:23,h:25,l:46,t:21,x:9,lb:12}}],
        map: {},
        form:{
          title:'',
          content:'',
          email:'',
          code:''
        },
        imageUrl:''
      }
    },
    methods:{
      //
      createMap(){
        this.map = new window.BMap.Map('allmap');
        var point = new window.BMap.Point(117.183064,31.848051);
        this.map.centerAndZoom(point, 14);
      },
      //
      setMapEvent(){
        this.map.enableDragging();//()
        this.map.enableScrollWheelZoom();//
        this.map.enableDoubleClickZoom();//()
        this.map.enableKeyboard();//
      },
      //
      addMapControl(){
        var ctrl_nav = new window.BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
        this.map.addControl(ctrl_nav);
        //
        var ctrl_ove = new window.BMap.OverviewMapControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,isOpen:1});
        this.map.addControl(ctrl_ove);
        //
        var ctrl_sca = new window.BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
        this.map.addControl(ctrl_sca);
      },
      //marker
      addMarker(){
        for(var i=0;i<this.markerArr.length;i++){
          var json = this.markerArr[i];
          var p0 = json.point.split("|")[0];
          var p1 = json.point.split("|")[1];
          var point = new window.BMap.Point(p0,p1);
          var iconImg = this.createIcon(json.icon);

          var myIcon = new window.BMap.Icon("markers.png", new BMap.Size(23, 25), {
            // 
            // 
            // 1025
            // 
            anchor: new BMap.Size(10, 25),
            // 
            // 
            // css sprites
            imageOffset: new window.BMap.Size(0, 0 - 1 * 25)   // 
          });

          var marker = new window.BMap.Marker(point,{icon:iconImg});
          var iw = this.createInfoWindow(i);
          var label = new window.BMap.Label(json.title,{"offset":new BMap.Size(json.icon.lb-json.icon.x+10,-20)});
          marker.setLabel(label);
          this.map.addOverlay(marker);
          label.setStyle({
            borderColor:"#808080",
            color:"#333",
            cursor:"pointer"
          });
          var $this=this;
          (function(){
            var index = i;
            var _iw = $this.createInfoWindow(i);
            var _marker = marker;
            _marker.addEventListener("click",function(){
              this.openInfoWindow(_iw);
            });
            _iw.addEventListener("open",function(){
              _marker.getLabel().hide();
            })
            _iw.addEventListener("close",function(){
              _marker.getLabel().show();
            })
            label.addEventListener("click",function(){
              _marker.openInfoWindow(_iw);
            })
            if(!!json.isOpen){
              label.hide();
              _marker.openInfoWindow(_iw);
            }
          })()
        }
      },
      //InfoWindow
      createInfoWindow(i){
        var json = this.markerArr[i];
        var iw = new BMap.InfoWindow("<b class='iw_poi_title' title='" + json.title + "'>" + json.title + "</b><div class='iw_poi_content'>"+json.content+"</div>");
        return iw;
      },
      //Icon
      createIcon(json){
        var icon = new BMap.Icon("http://app.baidu.com/map/images/us_mk_icon.png", new BMap.Size(json.w,json.h),{imageOffset: new BMap.Size(-json.l,-json.t),infoWindowOffset:new BMap.Size(json.lb+5,1),offset:new BMap.Size(json.x,json.h)})
        return icon;
      },
      getCaptcha(){
        this.$http.get(api+'/captcha', {
            params:{ran: Math.random()},
            responseType: 'arraybuffer'
          }).then(response => {
          return 'data:image/png;base64,' + btoa(new Uint8Array(response.data).reduce((data, byte) => data + String.fromCharCode(byte), ''))
          }).then(data => {
              this.imageUrl=data;
          })
      },
      onSubmit(){
        this.$http.post(api+'/saveMessage',this.form).then(function(response){
        if(response.data.code=='200'){
          this.$message({
            message: '',
            type: 'success'
          });
        }else{
          this.$message({
            message: response.data.message,
            type: 'warning'
          });
        }
        });
      }
    },
    components:{
      'carousel': carousel
    },
    mounted () {
      this.createMap();//
      this.setMapEvent();//
      this.addMapControl();//
      this.addMarker();//marker
      this.getCaptcha();
    },
  }
</script>



