<template>
  <div style="text-align:center;margin-top: 2rem">
    <el-row>
      <el-col :span="10" :offset="6">
        <el-form ref="form"  :model="form" label-width="100px">
          <el-form-item prop="phone" label="">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>

          <el-form-item  prop="email" label="" :rules="[{ required: true, message: '', trigger: 'blur' },{ type: 'email', message: '', trigger: ['blur', 'change'] }]">
            <el-input v-model="form.email" ></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="form.fax"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="form.address"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name:"contactInformation",
    inject: ['reload'],
    data() {
      return {
        form: {
          phone: '',
          email: '',
          fax: '',
          address: '',
        }
      }
    },
    methods: {
      onSubmit() {
        this.$http.post(api+'/manager/saveContactUs',this.form).then(function(response){
          if(response.data.code='200'){
            this.reload();
          }
        });
      }
    },
    mounted () {
      this.$http.get(api+'/manager/getContactUs').then(function(response){
        if(response.status='200'){
            this.form = response.data;
        }
      });
    }
  }
</script>



